<template>
	<div>
		<p>curriculum vitae123</p>
		<el-upload action="/" ref="upload" accept=".txt" :before-upload="beforeUpload">
			<el-button type="primary">上传文件</el-button>
		</el-upload>
		{{this.textData}}
	</div>
</template>
<script>
	export default {
		data() {
			return {
				textData: ''
			}
		},
		beforeCreate() {
			// 读取文件
			FileReader.prototype.reading = function({
				encode
			} = pms) {
				let bytes = new Uint8Array(this.result); //无符号整型数组
				let text = new TextDecoder(encode || 'UTF-8').decode(bytes);
				return text;
			};
			/* 重写readAsBinaryString函数 */
			FileReader.prototype.readAsBinaryString = function(f) {
				if (!this.onload) //如果this未重写onload函数，则创建一个公共处理方式
					this.onload = e => { //在this.onload函数中，完成公共处理
						let rs = this.reading();
						console.log(rs);
					};
				this.readAsArrayBuffer(f); //内部会回调this.onload方法
			};
		},
		methods: {
			getInputURL(file) {
				var url = null;
				url = window.webkitURL.createObjectURL(file);
				console.log(url)
				return url;
			},
			beforeUpload(file) {
				console.log('选择了文件beforeUpload')
				// 读取数据
				console.log(this.getInputURL(file))
				this.read(file);
				return false
			},
			read(f) {
				let rd = new FileReader();
				rd.onload = e => {
					//this.readAsArrayBuffer函数内，会回调this.onload函数。在这里处理结果
					let cont = rd.reading({
						encode: 'UTF-8'
					});
					console.log(cont);
					let formerData = this.textData;
					this.textData = formerData + "\n" + cont;
				};
				rd.readAsBinaryString(f);
			}
		}
	}
</script>
